import React, { useCallback } from "react";
import { NavLink, useNavigate } from "react-router-dom";
import styled from "styled-components";
import crypto from 'crypto-js';
import { useSelector, useDispatch } from "react-redux";
import { postLogin } from '../slices/MemberSlice';

import regexHelper from '../libs/RegexHelper';
import Spinner from '../components/Spinner';

const LoginContainer = styled.div`
    width: 100%;

    h3 {
        padding: 15% 0;
        display: flex;
        justify-content: center;
    }

    .login_content {
        width: 80%;
        display: flex;
        margin: 0 auto;
        flex-direction: column;
        flex-wrap: wrap;
        align-content: center;

        form {
            width: 100%;

            .input_text {
                width: 90%;
                height: 16px;
                padding: 5%;
                border: solid 1px #eee;
                border-radius: 1mm;
            }

            .login {
                font-size: 16px;
                margin-top: 10%;
                width: 100%;
                height: 2.4em;
                border: none;
                border-radius: 1mm;
                color: var(--white);
                background-color: var(--blue);
            }

            .err_msg {
                color: red;
                display: flex;
                font-size: 14px;
                padding: 4% 0 4% 0;
                margin-bottom: 2%;
            }
        }

        p {
            color: red;
            padding-top: 4%;
        }
    }

    .find_button_area {
        display: flex;
        flex-flow: row wrap;
        justify-content: space-between;

        .find_button {
            font-size: 16px;
            margin: 5% 0;
            width: 47%;
            height: 2.4em;
            border: none;
            border-radius: 1mm;
            color: var(--gray);
            background-color: #eee;
        }
    }

    .signup {
        font-size: 16px;
        width: 100%;
        height: 2.4em;
        border: none;
        border-radius: 1mm;
        color: var(--white);
        background-color: var(--blue);
    }
`;

const Login = () => {
    //     
    const navigate = useNavigate();
    //     
    const dispatch = useDispatch();
    const { loading } = useSelector((state) => state.member);

    /** input  onBlur  */
    const onBlur = useCallback(e => {
        //    
        try {
            switch(e.target.name) {
                case 'userid':
                    regexHelper.value(e.target.value, ' .');
                    regexHelper.engNum(e.target.value,' ,   .');
                break;
                case 'password':
                    regexHelper.value(e.target.value, ' ');
                break;
                default:
                break;
            }
        } catch (err) {
            console.log(err);
            throw err;
        }
    }, []);

    /** form submit  */
    const loginSubmit = useCallback(e => {
        e.preventDefault();

        //  
        const current = e.target;
        const userid = current.userid.value;
        let password = current.password.value;

        /**  _crypto-js  */
        // AES  -->   
        const secretKey = process.env.REACT_APP_CRYPTO_KEY;
        password = crypto.AES.encrypt(password, secretKey).toString();
    

        //    
        dispatch(postLogin({
            userid: userid,
            password: password,
        }))
        .unwrap()
        .then(() => {
            //   
            navigate('/');
        })
        .catch(err => {
            //   
            alert(err.data.rtmsg);
        });
    }, [dispatch, navigate]);

    return (
        <>
            <Spinner visible={loading}/>
            
            <LoginContainer>
                <div className="login_content">
                    <h3 className="font2"></h3>
                    <form onSubmit={loginSubmit}>
                        <input type="text" name="userid" onBlur={onBlur} className="input_text" placeholder=""></input>
                        <span id="err_id" className="err_msg"> .</span>

                        <input type="password" name="password" onBlur={onBlur} className="input_text" placeholder=""></input>
                        <span id="err_pw" className="err_msg"> .</span>

                        <button type="submit" name="login" value="login" className="login"></button>
                    </form>

                    <div className="find_button_area">
                        <button type="button" name="find_id" value="find_id" className="find_button"> </button>
                        <button type="button" name="find_pw" value="find_pw" className="find_button"> </button>
                    </div>

                    <h3 className="font2">  ?</h3>

                    <NavLink to="/signup">
                        <button type="button" id="signup" className="signup"></button>
                    </NavLink>
                </div>
            </LoginContainer>
        </>
    );
};

export default Login;
