import React, { useEffect, useCallback } from 'react';
import { useNavigate } from 'react-router-dom';
import styled, { keyframes } from 'styled-components';
import { useSelector, useDispatch } from 'react-redux';
import { getIsLogin, deleteLogin } from '../../slices/MemberSlice';

import Spinner from '../Spinner';

const fadeIn = keyframes`
    from{
        transform: translateX(100%);
    }
    to{
        transform: translateX(0);
    }
`;

const SidebarContainer = styled.div`
    width: 100%;
    height: 97vh;
    position: absolute;
    top: 7vh;
    background-color: rgba(255,255,255,0);
    z-index: 80;
    animation: ${fadeIn} 0.3s;

    .sidebar_content{
        box-sizing: border-box;
        width: 70%;
        margin-left: 30%;
        height: 97vh;

        background-color: var(--white);
        padding: 20px 20px;
        .back{
            width: 100%;
            
            background-color: black;
        }
        .login{
            background-color: white;
            height: 15%;
            display: flex;
            justify-content: center;
            align-items: center;
        }
        .user_inform{
            background-color: white;
            height: 15%;
            display: flex;
            justify-content: center;
            align-items: center;
            .profile_img{
                background-color: tomato;
                width: 70px;
                height: 70px;
                border-radius: 50%;
                img{
                    
                }
            }
            .profile_text{
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center; 
            }
        }
        .menu{
            margin: 15px 0;
            li{
                display: block;
                width: 100%;
                padding: 15px 0;
            }//li end
        }//menu end
    }//sidebar_content end
`;

const Sidebar = ({setShowSidebar}) => {
    //     
    const navigate = useNavigate();
    //     
    const dispatch = useDispatch();
    const { data, loading } = useSelector((state) => state.member);
    
    /**       */
    useEffect(() => {
        dispatch(getIsLogin());
    }, [dispatch]);

    /** logout    */
    const logout = useCallback(e => {
        e.preventDefault();
        //    
        dispatch(deleteLogin())
        .unwrap()
        .then(() => {
            //   
            navigate('/');
            setShowSidebar(false);
        })
        .catch(() => {
            //   
            alert(' .    .');
        });
    }, [dispatch, navigate, setShowSidebar]);

    /**    sidebar  */
    const movePage=useCallback(e => {
        e.preventDefault();
        navigate(e.target.dataset.path);
        setShowSidebar(false);
    }, [navigate, setShowSidebar]);

    /**    sidebar  +  */
    const movePage2=useCallback(e => {
        e.preventDefault();
        if(data) { //  
            navigate(e.target.dataset.path);
            setShowSidebar(false);
        } else { //  
            alert(" .");
            navigate("/login");
            setShowSidebar(false);
        }
    }, [data, navigate, setShowSidebar]);

    // sidebar unmount fadeout ??
    // useEffect(()=>{
    //     return(()=>{

    //     })
    // }, [])

    return (
        <>
            <Spinner visible={loading}/>

            <SidebarContainer>
                <div className="sidebar_content">
                <div className="back"></div>
                {
                    //     
                    data ? ( 
                        <div className="user_inform" data-path="/userinfo" onClick={movePage}>
                            <div className='profile_img'>
                                {/*      ! */}
                                {/*      ~ */}
                                <img src={data.item.profile_img} alt="img" />
                                {/* {console.log(data)} */}
                            </div>
                            <div className="profile_text">
                                <h1 className="font2">Hello,</h1>
                                <h1 className="font2">{data.item.userid}!</h1>
                            </div>
                        </div>
                        ) : (
                        <div className='login' data-path='/login' onClick={movePage}>/</div>
                        )
                }
                {/* menu  */}
                <ul className='menu'>
                <li onClick={movePage2} data-path='/mylike'> </li>
                <li onClick={movePage2} data-path='/myreview'> </li>
                <li onClick={movePage2} data-path='/myqna'> </li>
                <li onClick={movePage} data-path='/tourkit'></li>
                <li onClick={movePage} data-path='/service'></li>
                {
                    //     
                    data ? (
                    <li>
                        <button type="button" name="logout" className="logout" onClick={logout}></button>
                    </li>) : ''
                }
                </ul>
                </div>
            </SidebarContainer>
        </>
    );
}; 

export default Sidebar;