import BadRequestException from '../exceptions/BadRequestException.js';

class RegexHelper {
    /**
     *    .
     * @param  {string} content  
     * @param  {string} msg          
     */
    value(content, msg) {
        if (content === undefined || content === null || (typeof content === 'string' && content.trim().length === 0)) {
            throw new BadRequestException(msg);
        }

        return true;
    }

    /**
     *     .
     * @param  {string} content     
     * @param  {int} len            
     * @param  {string} msg            
     */
    maxLength(content, len, msg) {
        if (!this.value(content) || content.length > len) {
            throw new BadRequestException(msg);
        }

        return true;
    }

    /**
     *     .
     * @param  {string} content   
     * @param  {int} len          
     * @param  {string} msg          
     */
    minLength(content, len, msg) {
        if (!this.value(content) || content.length < len) {
            throw new BadRequestException(msg);
        }

        return true;
    }

    /**
     *    .
     * @param  {string} origin  
     * @param  {string} compare  
     * @param  {string} msg         
     */
    compareTo(origin, compare, msg) {
        var src = origin.trim(); //  .
        var dsc = compare.trim(); //   .

        if (src !== dsc) {
            throw new BadRequestException(msg);
        }

        return true; //  
    }

    /**
     *    .
     * @param  {string} content   
     * @param  {string} msg        
     * @param  {object} regexExpr  
     */
    field(content, msg, regexExpr) {
        const src = typeof content == 'string' ? content.trim() : content;

        //       ?
        if (!src || !regexExpr.test(src)) {
            throw new BadRequestException(msg);
        }

        return true;
    }

    /**
     *      field()  .
     * @param  {string} content   
     * @param  {string} msg         
     */
    num(content, msg) {
        return this.field(content, msg, /^[0-9]*$/);
    }

    /**
     *      field()  .
     * @param  {string} content   
     * @param  {string} msg         
     */
    eng(content, msg) {
        return this.field(content, msg, /^[a-zA-Z]*$/);
    }

    /**
     *      field()  .
     * @param  {string} content   
     * @param  {string} msg         
     */
    kor(content, msg) {
        return this.field(content, msg, /^[--]*$/);
    }

    /**
     *       field()  .
     * @param  {string} content   
     * @param  {string} msg         
     */
    engNum(content, msg) {
        return this.field(content, msg, /^[a-zA-Z0-9]*$/);
    }

    /**
     *       field()  .
     * @param  {string} content   
     * @param  {string} msg         
     */
    korNum(content, msg) {
        return this.field(content, msg, /^[--0-9]*$/);
    }

    /**
     *     field()  .
     * @param  {string} content   
     * @param  {string} msg         
     */
    email(content, msg) {
        return this.field(content, msg, /^([\w-]+(?:\.[\w-]+)*)@((?:[\w-]+\.)*\w[\w-]{0,66})\.([a-z]{2,6}(?:\.[a-z]{2})?)$/i);
    }

    /**
     *      field()  .
     * @param  {string} content   
     * @param  {string} msg         
     */
    cellphone(content, msg) {
        return this.field(content, msg, /^01(?:0|1|[6-9])(?:\d{3}|\d{4})\d{4}$/);
    }

    /**
     *     field()  .
     * @param  {string} content   
     * @param  {string} msg         
     */
    telphone(content, msg) {
        return this.field(content, msg, /^\d{2,3}\d{3,4}\d{4}$/);
    }

    /**
     *         
     * @param  {string} content   
     * @param  {string} msg           
     */
    phone(content, msg) {
        var check1 = /^01(?:0|1|[6-9])(?:\d{3}|\d{4})\d{4}$/; //  
        var check2 = /^\d{2,3}\d{3,4}\d{4}$/; //  

        var src = content.trim(); //  .

        //  ,                 ?
        if (!src || (!check1.test(src) && !check2.test(src))) {
            throw new BadRequestException(msg);
        }
        return true; //  
    }

    date(content,msg){
        return this.field(content, msg, /^\d{4}-(0[1-9]|1[012])-(0[1-9]|[12][0-9]|3[01])$/);
    }
}

export default new RegexHelper();