import React, { useCallback } from "react";
import styled from "styled-components";
import RegexHelper from '../libs/RegexHelper';
import { useNavigate } from 'react-router-dom';

import crypto from 'crypto-js';
import axios from "axios";
// import Arrow from "../assets/icon/arrow.png";

const SignupContainer = styled.div`
    width: 100%;
    padding-top: 20%;
    padding-bottom: 20%;

    h3 {
        padding-bottom: 12%;
        display: flex;
        justify-content: center;
    }

    .signup_content {
        width: 80%;
        display: flex;
        margin: 0 auto;
        flex-direction: column;
        flex-wrap: wrap;
        align-content: center;

        form {
            width: 100%;

            .message {
                /* display: flex; */
                padding-bottom: 2%;

                span{
                    font-size: 14px;
                    color: red;

                }
            }

            label {
                font-size: 14px;
            }

            /* select    */
            select {
                appearance: none;
                -moz-appearance: none; /* Firefox */
                -webkit-appearance: none; /* Safari and Chrome */
                color: var(--gray)
            }

            /* select IE    */
            select::-ms-expand {
                display: none; /* IE 10, 11 */
            }

            .err_msg {
                color: red;
                display: flex;
                font-size: 14px;
                padding: 4% 0 4% 0;
            }

            .input_text {
                width: 90%;
                height: 16px;
                padding: 5%;
                border: solid 1px #eee;
                border-radius: 1mm;
            }

            .birth_area {
                width: 100%;
                display: flex;
                justify-content: space-between;

                .user_birth {
                    width: 20%;
                    height: 16px;
                    padding: 5%;
                    border: solid 1px #eee;
                    border-radius: 1mm;
                }

                .user_mm {
                    width: 30%;
                    height: auto;
                    padding: 4%;
                    border: solid 1px #eee;
                    border-radius: 1mm;
                }
            }

            .email_area {
                width: 100%;
                
                .input_box{
                    display: flex;
                    justify-content: space-between;
                }

                .user_email {
                    width: 40%;
                    height: 16px;
                    padding: 5%;
                    border: solid 1px #eee;
                    border-radius: 1mm;
                }

                .user_email_sel {
                    width: 45%;
                    height: auto;
                    padding: 5%;
                    border: solid 1px #eee;
                    border-radius: 1mm;
                }
            }

            .signup {
                font-size: 16px;
                width: 100%;
                height: 2.4em;
                border: none;
                border-radius: 1mm;
                color: var(--white);
                background-color: var(--blue);
            }
        }
    }
`;

const Signup = () => {
    const navigate = useNavigate();

    /**      */
    const month = [];
    for (let i = 1; i < 13; i++) month.push(i);

    //   
    let userid = null;
    let password = null;
    let passwordCheck = null;
    let username = null;
    let birthday = null;
    let email = null;

    /**input onBlur  */
    const onBlur = useCallback(e => {
        try {            
            switch (e.target.id) {
                case 'userid':
                    RegexHelper.value(e.target.value,' ');
                    RegexHelper.engNum(e.target.value,' , .');
                break;
                case 'password':
                    RegexHelper.value(e.target.value,' ');
                break;
                case 'passwordCheck':
                    RegexHelper.value(e.target.value,'  ');
                break;
                case 'username':
                    RegexHelper.value(e.target.value,' ');
                break;
                case 'birth_year':
                    if(e.target.value){
                        RegexHelper.value(e.target.value,' ');
                    }
                break;
                case 'birth_month':
                    if(e.target.value){
                        RegexHelper.value(e.target.value,'  ');
                    }
                break;
                case 'birth_day':
                    if(e.target.value){
                        RegexHelper.value(e.target.value,'  ');
                    }
                break;
                case 'email':
                    RegexHelper.value(e.target.value, ' .');
                    RegexHelper.engNum(e.target.value, ' , .');
                break;
                default:
                break;
            }
                    
        } catch (error) {
            console.log(error);
            throw error;
        }
    },[]);

    /**submit :   ,  post */
    const onSubmit = async(e) => {
        e.preventDefault();

        const current = e.target;
        userid = current.userid.value;
        password = current.password.value;
        passwordCheck = current.passwordCheck.value;
        username = current.username.value;
        birthday = current.birth_year.value+'-'+current.birth_month.value+'-'+current.birth_day.value;
        email = current.input_email.value+current.input_domain.value;
        
        /***/
        try {
            RegexHelper.value(userid,' ');
            RegexHelper.engNum(userid,' , .');
            RegexHelper.value(password,' ');          
            RegexHelper.value(passwordCheck,'  ');
            RegexHelper.value(username,' ');
            if (birthday) {
                RegexHelper.value(birthday,' ');
            };
            RegexHelper.value(email, ' .');
            RegexHelper.email(email,'  .');

        } catch (error) {
            console.log(error);
            throw error;
        }

        /** _crypto-js  */
        // AES  
        const secretKey = process.env.REACT_APP_CRYPTO_KEY;
        password = crypto.AES.encrypt(password, secretKey).toString();
        passwordCheck = crypto.AES.encrypt(passwordCheck, secretKey).toString();
        
        /**      */
        const input_data={
            userid : userid,
            password : password,
            passwordCheck: passwordCheck,
            username : username,
            birthday : birthday,
            email : email
        }

        let json = null;
        try {
            json = await axios.post("/members",input_data)
        } catch (error) {
            console.log(error);
            window.alert(`[ ${e.response.status} ] ${e.response.statusText} \n ${e.message}`);
        }
        window.alert(json.data.item + "  ")
        console.log(json);
        navigate("/");
    };

    return (
        <SignupContainer>
            <div className="signup_content">
                <h3 className="headfont"></h3>

                <form onSubmit={onSubmit}>
                    {/*  */}
                    <div>
                        <label htmlFor="userid" className="message"> <span>*</span></label>
                        <input id="userid" name="userid" type="text" onBlur={onBlur} className="input_text" ></input>
                        <span id="err_id" className="err_msg">  . </span>
                        <br />
                    </div>

                    {/*  */}
                    <div >
                    <label htmlFor="passord" className="message"> <span>*</span></label>
                    <input id="password" name="password" type="password" onBlur={onBlur} className="input_text" placeholder="+ 8~16"></input>
                    <span id="err_pw" className="err_msg">  . </span>
                    <br />
                    </div>

                    {/*   */}
                    <div>
                        <label htmlFor="passordCheck" className="message"> <span>*</span></label>
                        <input id="passwordCheck" name="passwordCheck" type="password" onBlur={onBlur} className="input_text"/>
                        <span id="err_pw_check" className="err_msg">   .</span>
                        <br />
                    </div>

                    {/*  */}
                    <div>
                        <label htmlFor="username" className="message"><span>*</span></label>
                        <input id="username" name="username" type="text" className="input_text" onBlur={onBlur}></input>
                        <span id="err_name" className="err_msg">  .</span>
                        <br />
                    </div>

                    {/*  */}
                    <div>
                        <label htmlFor="birth_year" className="message"></label>
                        <div className="birth_area">
                            <input id="birth_year" name="birth_year" type="text" placeholder="(4)" className="user_birth" maxLength={4} onBlur={onBlur} />
                            <select id="birth_month" name="birth_month" className="user_mm" placeholder="" onBlur={onBlur}>
                                <option value=""></option>
                                {month.map((v, i) => {
                                    return (
                                        <option value={v} key={i}>
                                            {v}
                                        </option>
                                    );
                                })}
                            </select>
                            <input id="birth_day" name="birth_day" type="text" placeholder="" className="user_birth" maxLength={2} onBlur={onBlur}/>
                        </div>
                        <span id="err_birth" className="err_msg">  . </span>
                        <br />
                    </div>

                    {/*  */}
                    <div className="email_area">
                        <label htmlFor="user_email" className="message"></label>
                        <div className="input_box">
                            <input id="input_email" name="input_email" type="text"  onBlur={onBlur} className="user_email" maxLength={20}/>
                            <select id="input_domain" name="input_domain" type="text" className="user_email_sel">
                                {/*      */}
                                <option value="@gmail.com">@gmail.com</option>
                                <option value="@naver.com">@naver.com</option>
                                <option value="@daum.com">@daum.com</option>
                            </select>
                        </div>
                    </div>
                    <span id="err_email" className="err_msg">   .</span>
                    <br />

                    <button type="submit" id="signup" className="signup">
                        
                    </button>
                </form>
            </div>
        </SignupContainer>
    );
};

export default Signup;
